import React, { useState } from 'react';
import '../styles/Puzzle.css';

const Puzzle = ({ puzzle, gameState = {}, onSolve, onClose }) => {
  const [input, setInput] = useState('');
  const [response, setResponse] = useState('');

  const handleSolve = () => {
    //    (   ..)
    if (puzzle.interactive && puzzle.answers) {
      const lowerInput = input.toLowerCase().trim();
      
      //      (//)
      const foundAnswer = Object.entries(puzzle.answers).find(([key]) => 
        lowerInput.includes(key.toLowerCase())
      ) || ['default', puzzle.answers.default];
      
      const [answerKey, answer] = foundAnswer;
  
      //   ( )
      if (answer.condition && !answer.condition(gameState)) {
        setResponse('    ');
        return;
      }
  
      //   (    )
      const responseText = typeof answer.response === 'function' 
        ? answer.response(gameState) 
        : answer.response;
      
      setResponse(responseText);
  
      //      
      if (answer.reward && (!answer.condition || answer.condition(gameState))) {
        //  onSolve      
        onSolve({
          reward: answer.reward,
          stateUpdates: answer.onSolve ? answer.onSolve(gameState) : null
        });
      }
    } 
    //       (,   ..)
    else if (input === puzzle.answer) {
      setResponse(' !');
      onSolve({
        reward: puzzle.reward,
        stateUpdates: puzzle.onSolve ? puzzle.onSolve(gameState) : null
      });
    } else {
      setResponse(' !   .');
    }
  };

  return (
    <div className="puzzle-overlay">
      <div className="puzzle">
        <h2>{puzzle.title}</h2>
        <img className='puzzle-image' src={require(`../../public/images/${puzzle.image}`)}/>
        <p>{typeof puzzle.description === 'function' 
          ? puzzle.description(gameState) 
          : puzzle.description}
        </p>

        
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder=" ..."
        />
        
        <div className="puzzle-buttons">
          <button onClick={handleSolve}>
            {puzzle.interactive ? '' : ''}
          </button>
          <button onClick={onClose}></button>
        </div>
        
        {response && <div className="puzzle-response">{response}</div>}
        <details>
          <summary></summary>
          <p>{puzzle.help}</p>
        </details>
      </div>
    </div>
  );
};

export default Puzzle;